*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.tranparentbox{
    border: 3px solid rgb(0, 0, 0);
    width: 100%;
    margin: 30px 0 30px 0;
    line-height: 27px;
    padding: 30px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}
.tranparentbox:hover{
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
    background: red;
    -webkit-transform: scale(1);
            transform: scale(1);
    box-shadow: 0 3px 6px rgb(190, 0, 0), 0 3px 6px rgb(190, 0, 0);
}

